
@use "common";
@use "part-product";
@use "part-cart-icon";
@use "toast";



:root {
    --bg: #ee4d2d;
}
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
  font-size: 10px;
}

body {
  background-color: #f5f5f5;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 10px;
}


/* Response cho Phone (Dưới 768px) */
@media (max-width: 768px) {
 .container {
        max-width: 100%; /* Cho phép tràn toàn màn hình điện thoại */
        padding: 0 5px;  /* Thu hẹp lề hai bên để tối ưu không gian hiển thị */
    }
}

/* Header */
.header-orange {
  background-color: var(--bg);
  color: white;
  padding: 10px 0;
}

.top-nav {
  display: flex;
  justify-content: space-between;
  font-size: 13px;
  margin-bottom: 15px;
}

.search-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.logo {
  font-size: 40px;
  font-weight: bold;
  flex: 1;
}

.search-bar-container {
  flex: 4;
  margin: 0 40px;
}

.search-input-group {
  background: white;
  display: flex;
  padding: 3px;
  border-radius: 2px;
}

.search-input-group input {
  border: none;
  flex: 1;
  padding: 10px;
  outline: none;
}

.btn-search {
  background: #ee4d2d;
  border: none;
  color: white;
  padding: 0 20px;
  cursor: pointer;
}

.search-tags {
  font-size: 12px;
  margin-top: 5px;
}

.search-tags span {
  margin-right: 12px;
  cursor: pointer;
}



/* Response cho Phone (Dưới 768px) */
@media (max-width: 768px) {
    /* 1. Ẩn các phần rườm rà */
    .top-nav, .search-tags {
        display: none;
    }

    /* 2. Căn chỉnh Header và Logo */
    .header-orange {
        padding: 10px 0 35px 0; /* Tăng padding bottom để có chỗ cho giỏ hàng nằm dưới */
    }

    .search-section {
        display: flex;
        flex-direction: column; /* Xếp Logo và Thanh tìm kiếm theo hàng dọc */
        align-items: flex-start;
        position: relative; /* Làm gốc tọa độ cho giỏ hàng */
    }

    .logo {
        font-size: 24px;
        margin-bottom: 8px;
    }

    /* 3. Thanh tìm kiếm tràn ngang */
    .search-bar-container {
        width: 100%;
        margin: 0;
    }

    .search-input-group input {
        padding: 8px;
        font-size: 13px;
    }

    /* 4. Đưa giỏ hàng xuống dưới nút tìm kiếm */
    .cart-icon {
        position: absolute;
        /* Căn phải bằng với lề phải của nút search */
        right: 5px; 
        /* Đẩy xuống dưới thanh tìm kiếm (thanh tìm kiếm thường cao ~40px + logo phía trên) */
        top: 100%; 
        margin-top: 4px; /* Khoảng cách nhỏ với thanh search */
        font-size: 22px;
    }
}

//======================================= E



/* Banner Placeholder */
.banner-placeholder {
  margin: 20px 0;
}

.fake-banner {
  width: 100%;
  height: 300px;
  background: #ddd;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px dashed #999;
}

/* Quick Links */
.quick-links {
  background: white;
  padding: 20px 0;
}

.link-wrapper {
  display: flex;
  justify-content: space-around;
  text-align: center;
}

.link-item img {
  width: 43px;
  height: 43px;
  display: block;
  margin: 0 auto 10px;
}

.link-item span {
  font-size: 13px;
  color: #333;
}

/* Categories */
.categories {
  background: white;
  margin-top: 20px;
  padding: 20px 0;
}

.section-title {
  padding-left: 10px;
  margin-bottom: 15px;
  color: #777;
  font-weight: normal;
}

.category-grid {
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  border: 1px solid #efefef;
}

.cat-item {
  border: 1px solid #efefef;
  text-align: center;
  padding: 10px 0;
  cursor: pointer;
}

.cat-item:hover {
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.cat-item img {
  width: 83px;
  height: 83px;
  object-fit: cover;
}

.cat-item span {
  display: block;
  font-size: 12px;
  color: #333;
  margin-top: 5px;
}


/* Response cho Phone (Dưới 768px) */
@media (max-width: 768px) {
    /* 1. Container tràn màn hình */
    .container {
        max-width: 100%;
        padding: 0 5px;
    }

    /* 2. Chia lại Danh mục thành 4 cột */
    .category-grid {
        grid-template-columns: repeat(4, 1fr); /* Hiển thị 4 item mỗi hàng */
    }

    /* 3. Tối ưu item danh mục trên mobile */
    .cat-item {
        padding: 8px 2px; /* Giảm padding để tiết kiệm diện tích */
    }

    .cat-item img {
        /* Thay vì cố định 83px, ta dùng % để ảnh tự co giãn theo cột */
        width: 80%; 
        height: auto;
        aspect-ratio: 1 / 1; /* Giữ ảnh luôn vuông */
        max-width: 83px; /* Không cho phép to quá 83px gốc */
    }

    .cat-item span {
        font-size: 11px; /* Giảm nhẹ size chữ cho cân đối */
        line-height: 1.2;
        height: 2.4em; /* Giới hạn độ cao chữ nếu tên quá dài */
        overflow: hidden;
    }

    /* 4. Chia sản phẩm thành 2 cột (như đã làm ở bước trước) */
    .product-grid {
        grid-template-columns: repeat(2, 1fr);
        gap: 8px;
    }
}

.myCartTitle {
    font-size: 18px;
    font-weight: bold;
}